</template>

<script>
import $ from 'jquery';
import tips from '@/assets/js/tips';

export default {
  name: "SystemMessageInput",
  data() {
    return {
      tips,
    }
  },
  methods: {
    // 发送系统消息
    sendSystemMessage() {
      let systemMessageInput = $('.system-message-input');
      let systemMessage = systemMessageInput.html();
      if (systemMessage && systemMessage !== '') {
        systemMessageInput.html('');
        this.$bus.$emit('handleSystemMessage', systemMessage);
      } else {
        this.$bus.$emit('addWarningTip', '系统消息不能为空!', 'warning')
      }
    }
  },
  beforeDestroy() {
    $('.send-button').off('click');
  }
}
</script>

<style scoped>

.system-message-input-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 320px;
  transition: all 0.3s;
}

/*输入框*/
.system-message-input {
  width: 100%;
  height: 280px;
  padding: 10px;
  font-size: 14px;
  outline: none;
  overflow-y: auto;
}

/*输入框滚动条*/
.system-message-input::-webkit-scrollbar {
  width: 10px;
}

.system-message-input::-webkit-scrollbar-track {
  background-color: #939393;
  border-radius: 10px;
}

.system-message-input::-webkit-scrollbar-thumb {
  background-color: #6b6b6b;
  border-radius: 10px;
}

.system-message-input::-webkit-scrollbar-thumb:hover {
  background-color: #626262;
}

.system-message-input::-webkit-scrollbar-thumb:active {
  background-color: #525252;
}

/*工具集合*/
.message-input-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 40px;
  background-color: transparent;
}

.message-tools {
  display: flex;
  align-items: center;
}

/*工具图标*/
.message-tools svg {
  width: 20px;
  height: 20px;
  vertical-align: -3px;
}

/*单个工具*/
.message-tool {
  margin-left: 15px;
  cursor: pointer;
}

.send-button {
  width: 50px;
  height: 26px;
  text-align: center;
  line-height: 26px;
  margin-right: 15px;
  border-radius: 4px;
  cursor: pointer;
}

</style>